<template>
    <div class="app">
        <div class="headB">
            <div class="moneyTitle"><img :src="sendUser.avatar" alt=""><span>{{sendUser.nickname}}的红包</span></div>
            <div class="moneyArt">恭喜发财，大吉大利</div>
            <div v-if="myself > 0" class="moneyNumberBox"><span>{{myself}}</span><i>{{sendUser.currency_name}}</i></div>
        </div>
        <p v-if="nextUser" class="art">{{sendUser.rule == 1 ? '最少' : '最大'}} / 下一个红包将由您发出</p>
        <ul class="moneyList">
            <li v-for="item in listData" class="ov">
                <div class="fl headImg"><img :src="item.avatar" alt=""></div>
                <div class="fl infoBox">
                    <p class="nameBox"><span class="name">{{item.nickname}}</span>
                        <span v-if="item.visitor"  class="label">游客</span>
                        <span v-else  class="groupNumber">群会员</span>
                    </p>
                    <p class="time">{{item.update_time}}</p>
                </div>
                <div class="fr eosNumber">
                    <p class="number" v-if="item.money > 0">{{item.money}}{{item.currency_name}}</p>
                    <p class="number" v-else>未结束</p>
                    <p class="less" v-if="item.lucky == 1">最少</p>
                    <p class="less" v-if="item.lucky == 2">最多</p>
                </div>
            </li>
        </ul>
    </div>

</template>

<script>
    import { redpacketLog} from '@/api/red'
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { List } from "vant";
    import 'vant/lib/list/style';
    export default {
        name: "Redpacketlog",
        components: {
            [List.name]: List,
        },
        data()
        {
            return {

                sendUser:{
                    avatar:'',
                    nicknam:'',
                    currency_name:'',
                    rule:1
                },
                listData:[],
                nextUser:false,
                myself:0


            }
        },

        mounted()
        {
            this.redpacket_id = this.$route.query.redpacketId
            this.getListData()
        },
        methods:{

            // 获取群信息
            getListData()
            {
                redpacketLog({redpacket_id:this.redpacket_id}).then(res => {

                    this.listData = res.list
                    this.sendUser = res.sendUser
                    this.nextUser = res.nextUser
                    this.myself = res.myself

                })
            }

        }
    }
</script>

<style scoped>
    @import "../../static/css/redpacketlog.css";
</style>



